    content: 'Greve, Denmark – We are tremendously happy and proud to announce the opening of the brand new Alefarm Brewing.\n\nWe have been on a journey that took nearly a year from finding the right location to getting all equipment installed and running smoothly. It has been quite the ride - fun, challenging and deeply rewarding.\n\nAt the new brewery, we will have a fixed line-up of beers supplemented by seasonal offerings, new releases and collaborations. The production is carried forward by an explorative, creative and spontaneous brewing philosophy.\n\nWe are delighted to welcome two new brewers and a brand manager to the Alefarm family. Every single person at Alefarm Brewing is a part of our common story and we are ecstatic to have such talented people join us.\n\nI would like to say a profound thank you to everyone involved in the process. Thank you for helping out, for believing in us and for lending an ear when times were rough.\n\nNow, good times await and excellent beers are to be made. We are so excited and look forward to welcoming you to the new brewery. Cheers!',
